<template>
	<section>
		<div class="killer">
			<img src="../assets/rickandborty.gif" alt="" class="rick">
			<pre>{{code}}</pre>
			<div v-html="styleCode"></div>
		</div>
	</section>
</template>			
<script>
	export default {
		name:'about',
		data () {
			return {
				code:``,
				mycode:`
					 /*
						大家好，在下提子杀手小药丸是也，
						这个背景色不符合我的killer气质，
						换一个
					 */
					.killer{
						background-color:#000;
						color:green;
					}
					/*咳咳..开个玩笑我是小清新风的*/
					.killer{
						background-color:pink;
						color:#000;
					}
					/*下面做下自我介绍*/
					
					/*
						个人技能:
							HTML/CSS/JavaScript/Vue/Webpacke/做饭
					*/
					/*
						个人爱好：
							吃提子/动漫/看书/喝茶
					*/
					/*这个时候要放一张照骗啦*/
					.killer{
						background:url(static/1.jpg);
						
					}
					/*就酱 白了个白~*/
					
				`
			}
		},
		created () {
			var n = 0;
			setInterval(() => {
				this.code = this.mycode.substring(0,n);
				// this.style = 
				n++;
				if(n===this.mycode.length) return;
			},60)
		},
		computed : {
			styleCode () {
				return '<style>' + this.code + '</style>'
			}
		}
	}
</script>
<style scoped>

	.killer{
		/*background:url(../assets/1.png);*/
		/*color: #000;*/
		margin:20px auto;
		width: 50%;
		height:600px;	
		/*background-color: pink;*/
		border: 1px solid #d2d1cd;
		box-shadow:0 2px 6px rgba(0,0,0,0.65);
		font-size: 14px;
	}
	.rick{
		width: 300px;
		/*border: 1px solid #000;*/
		border-radius: 50%;
		position: absolute;
		right:0;
		bottom:10px;
	}
	@media screen and (max-width: 1250px) {
    	.rick{
    		display: none;
    	}
	}
</style>


