<template>
	<div class="box">
		
		<div class="unit" id="unit">
		
		</div>
	</div>

</template>
<script>
	import $ from 'jquery'
	export default {
		name:'ball',
		mounted () {
			// var $unit = document.getElementById("#unit")
	var $unit = $(".unit");
	// 每一层需要的gird数量
	var arr = [12,10,8,6,4,1]
	// 动态创建一个球
	for(var i = 0;i<=5;i++){
		set(i,arr[i])
		// 第0层重叠了，去掉下半球的第0层
		if(i === 0) continue;
		set(-i,arr[i])
	}
	//总共有10层，所以每层360/2/10=18deg;
	//设置每层图片翻转度数函数，floor第几层，amount每层需要的图片数
	// rgb不支持小数，parseInt()
	function set(floor,amount) {
		for(var i = 0;i<amount;i++){
			$("<div class='grid'></div>").css({
				"transform":"rotateY("+ 360/amount * i +"deg) rotateX("+ floor*18 +"deg) translateZ(250px)",
				"background-color":"rgb("+parseInt(Math.random()*225)+","+ parseInt(Math.random()*225)+","+ parseInt(Math.random()*225)+")"
			}).appendTo($unit)
		}
	}

	// 鼠标，rotateX跟鼠标的clientY值有关，除可以让运动迟缓一点	
	$(document).mousemove(function(e){
		// console.log(e.clientY)
		$unit.css("transform","rotateX("+e.clientY / 10+"deg) rotateY("+e.clientX/10+"deg)")
	})
		}
	}
</script>
<style>
	.box{
		 margin: -280px auto;
	}
	 .unit{
	 	width: 100%;
	 	height: 100%;
	 	position: relative;
		margin:50px auto;
	 	/*当一个元素内部有3d,自己也要进行选择，一定要加上transform-style:preserve-3d,元素内部的3d变化将会保留*/
	 	transform-style:preserve-3d;
	 }
	 div.grid{
	 	width: 60px;
	 	height: 70px;
	 	background-color: pink;
	 	opacity: 0.4;
	 	position: absolute;
	 	top:50%;
	 	left:50%;
	 	margin-left:-30px;
	 	margin-top:-20px;
	 }
</style>


