<template>
	<div class="snake">
		<button class="begin" id="begin">ready go</button>
		<div class="map" id="map">
		</div>
		<h1 class="point" id="point">得分是</h1>
	</div>
</template>
<script>
	// import Game from './game.js'
	// import Sisi from './sisi.js'
	export default {
		name:'snake',
		mounted () {
			var $map = document.getElementById("map")	
			var $point = document.getElementById("point")	
			var $begin = document.getElementById("begin")	
			var snake; var food;
			var timer;
			var sum=0;
				function Food() {
					this.width=20; //宽度
     				this.height=20; //高度
     				this.position='absolute'; //定位方式
     				this.color='pink'; //食物颜色
     				this.isFood=null; //用于保存食物dom元素
     				this.x=0; 
     				this.y=0;
					this.isFood = null;
					// 显示
					this.show = function () {
						if (this.isFood == null) {
							this.isFood = document.createElement("div");
							this.isFood.style.width = this.width + 'px';
             				this.isFood.style.height = this.height + 'px';
             				this.isFood.style.position = this.position ;
             				this.isFood.style.backgroundColor = this.color ;
             				$map.appendChild(this.isFood)
						}
						this.x=Math.floor(Math.random()*24);
						this.y=Math.floor(Math.random()*21);
						this.isFood.style.left = this.x*this.width+'px';
        				this.isFood.style.top = this.y*this.height+'px';
					}
				}
			// snake
				function Snake(){
					this.width = 20;
					this.height = 20;
					this.position = 'absolute';
					this.direct='right';
					// [x轴坐标，y轴坐标，颜色，是否渲染]
					this.body=[[3,3,'#c4ffc4',null],[2,3,'green',null],[1,3,'green',null]];
					//方向
					this.setDirect = function (code) {
						switch(code){
							case 37:
								this.direct='left';
								break;
							case 38:
								this.direct='up';
								break;
							case 39:
								this.direct='right';
								break;
							case 40:
								this.direct='down';
								break;
						}
					}
					//显示
					this.show = function () {
						for(var i=0;i<this.body.length;i++){
							if(this.body[i][3]==null)
           				{
           				    this.body[i][3] = document.createElement('div');
           				    this.body[i][3].style.width = this.width +'px';
           				    this.body[i][3].style.height = this.height +'px';
           				    this.body[i][3].style.position = this.position;
           				    this.body[i][3].style.backgroundColor = this.body[i][2];
           				    $map.appendChild(this.body[i][3]);
           				}
           				this.body[i][3].style.left=this.body[i][0]*this.width+'px';
             			this.body[i][3].style.top=this.body[i][1]*this.height+'px';
						}
					}
				//动起来
				this.move = function () {
					//移动
					var len = this.body.length-1;
					for(var i=len;i>0;i--){
						this.body[i][0]=this.body[i-1][0]; 
             			this.body[i][1]=this.body[i-1][1];
					}
					switch(this.direct)
        				{
        				    case 'right':
        				        this.body[0][0]=this.body[0][0]+1;
        				        break;
        				    case 'down':
        				        this.body[0][1]=this.body[0][1]+1;
        				        break;
        				    case 'left':
        				        this.body[0][0]=this.body[0][0]-1;
        				        break;
        				    case 'up':
        				        this.body[0][1]=this.body[0][1]-1;
        				        break;
        				    default:
        				        return;
        				}
					this.show();
					// 撞墙
					if (this.body[0][0] < 0 || this.body[0][0] > 24 || this.body[0][1] <0 || this.body[0][1] >21) {
						clearInterval(timer);
						alert('哎唷( ⊙ o ⊙ )！撞墙了');
						return;
					}	
					// 吃食物
					if (this.body[0][0] == food.x && this.body[0][1] == food.y) {
						var x = this.body[length][0];
						var y = this.body[length][1];
						sum ++ ;
						$point.innerHTML="您的得分是："+sum+"分";
						this.body.push([x,y,'green',null])
						food.show();
					};
					// 碰到自己
					for(var i=len;i>1;i--){
						if (this.body[0][0]==this.body[i][0]&&this.body[0][1]==this.body[i][1]) {
							clearInterval(timer);
							alert("笨蛋^(*￣(oo)￣)^吃到自己啦")
							return;
						};
						
					}
					}
				}
				//调用
				$begin.onclick=function(){
					food = new Food();
					food.show()
					snake = new Snake();
					snake.show();
					// snake.move();
					timer = setInterval(function () {
						snake.move()
					},200)
					 // timer = setInterval('snake.move()',100);
					document.onkeydown = function (e) {
						var code;
						// console.log(e.keyCode)
						if (window.event) {
							code = window.event.keyCode;
							// console.log(code)
						}else{
							code = event.keyCode;
						}
						snake.setDirect(code);
					// console.log(snake.direct)
					}
				};
			}
	}
</script>
<style scoped>
	.snake{
		margin:-482px 500px;
		display: flex;
		/*justify-content:center;*/
		/*align-items:center; */
		width: 60%;
	}
	.map{
		width: 500px;
		height: 440px;
		background-color: #000;
		position: relative;
		overflow: hidden;
		margin-right: 10px;
	}
	.begin{
		margin: 10px 10px;
		width: 60px;
		height: 40px;
		border-radius: 4px;
	}
</style>


